import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import { useFormik } from 'formik';
import * as yup from 'yup';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';

import { Loader, LocationField, TextField } from '../components';

import { LocationUpdate, LocationQuery } from '../api';
import { useSnackbar } from '../hooks';
import { GeoPoint, UpdateLocation } from '../utils/dataTypes';
import { cleanObject } from '../utils/queryHelpers';

const validationSchema = yup.object({
  zipCode: yup.string().required('Zip Code is required'),
  name: yup.string().required('Name is required'),
  stateCode: yup.string().required('State Code is required'),
  stateName: yup.string().required('State Name is required'),
  location: yup
    .tuple([
      yup
        .number()
        .typeError('Latitude is required')
        .min(-90, 'Latitude must be grater than or equal: -90')
        .max(90, 'Latitude must be less than or equal: 90')
        .required('Location should be in format "latitude, longitude"'),
      yup
        .number()
        .typeError('Longitude is required')
        .min(-180, 'Longitude must be grater than or equal: -180')
        .max(180, 'Longitude must be less than or equal: 180')
        .required('Location should be in format "latitude, longitude"'),
    ])
    .required('Location is required'),
});

const initialValues = {
  id: '',
  zipCode: '',
  name: '',
  stateCode: '',
  stateName: '',
  location: [NaN, NaN] as GeoPoint,
};

export default function EditLocationForm() {
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const queryClient = useQueryClient();
  const setSnackbarMessage = useSnackbar();

  const {
    isLoading: loading,
    isFetching: fetching,
    data,
  } = useQuery({
    queryKey: ['location', pathname.split('/')[3]],
    queryFn: LocationQuery,
    keepPreviousData: true,
    retry: false,
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const { mutate, isLoading } = useMutation(LocationUpdate, {
    onSuccess: () => {
      setSnackbarMessage({
        severity: 'success',
        message: 'Saved',
      });
      queryClient.invalidateQueries(['locations']);
      navigate(-1);
    },
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<UpdateLocation>({
    initialValues: {
      ...initialValues,
      ...cleanObject(data, Object.keys(initialValues)),
    },
    enableReinitialize: true,
    validationSchema: validationSchema,
    onSubmit: (values) => {
      mutate(values);
    },
  });

  const handleClose = () => {
    navigate(-1);
  };

  return (
    <Box>
      <form onSubmit={formik.handleSubmit} onReset={formik.handleReset}>
        <Box sx={{ p: 3, display: 'grid' }}>
          <Box key="controls" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, width: '50%' }}>
              <TextField fieldName="name" label="Name" formik={formik} />
              <TextField
                fieldName="stateName"
                label="State Name"
                formik={formik}
              />
            </Box>
            <Box key="right" sx={{ p: 1, width: '50%' }}>
              <TextField fieldName="zipCode" label="Zip Code" formik={formik} />
              <TextField
                fieldName="stateCode"
                label="State Code"
                formik={formik}
              />
            </Box>
          </Box>
          <Box key="location" sx={{ marginTop: -2, display: 'inline-flex' }}>
            <LocationField
              fieldName="location"
              label={['Latitude', 'Longitude']}
              formik={formik}
            />
          </Box>
          <Box key="buttons" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="submit"
                disabled={!formik.dirty}
              >
                Save
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={isLoading || loading || fetching} />
    </Box>
  );
}
